import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '',
    name: 'Default',
    component: () => import('../views/Default.vue'),
    meta: {
      title: 'Rabbit'
    }
  },{
    path: '/home',
    name: 'Home',
    component: () => import('../views/Home.vue'),
    meta: {
      title: '首页'
    }
  },{
    path: '/article:index',
    name: 'Article',
    component: () => import('../components/homeArticleComponents/ArticleBody.vue'),
    meta: {
      title: '文章详情'
    }
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/About.vue'),
    meta: {
      title: '关于'
    }
  },
  {
    path: '/summon',
    name: 'Summon',
    component: () => import('../components/choujiangComponents/Summon.vue'),
    meta: {
      title: '抽奖模拟'
    }
  },
  {
    path: '/ballgame',
    name: 'ballAnimation',
    component: () => import('../components/ballComponents/BallAnimation.vue'),
    meta: {
      title: '碰撞球'
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
  // linkActiveClass: 'active'
})

router.beforeEach((to, from, next) => {
  //router全局前置钩子用于更改page的title
  document.title = to.matched[0].meta.title;
  next();
})

export default router
